@mixin trancTextOneLine() {
    /* Nécessaires pour text-overflow */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    display: inline-block;
}

@mixin del-X-marge {
    margin-right: -15px;
    margin-left: -15px;
    border-right: none;
    border-left: none;
}